import React from 'react';
import './index.scss';

import { Layout, Table } from '../../components';

import enemys from '../../data/enemy/enemys.json';

export default class AKEnemys extends React.Component {
  render() {
    const columns = [
      {
        title: '',
        key: 'name',
        dataIndex: 'name',
        sortable: true,
      },
      {
        title: 'Level',
        key: 'level',
        dataIndex: 'level',
        sortable: true,
      },
      {
        title: '',
        key: 'hp',
        dataIndex: 'hp',
        sortable: true,
      },
      {
        title: '',
        key: 'atk',
        dataIndex: 'atk',
        sortable: true,
      },
      {
        title: '',
        key: 'def',
        dataIndex: 'def',
        sortable: true,
      },
      {
        title: '',
        key: 'magicResistance',
        dataIndex: 'magicResistance',
        sortable: true,
      },
      {
        title: '',
        key: 'moveSpeed',
        dataIndex: 'moveSpeed',
        sortable: true,
      },
      {
        title: '',
        key: 'attackSpeed',
        dataIndex: 'attackSpeed',
        sortable: true,
      },
      {
        title: '',
        key: 'lifePointReduce',
        dataIndex: 'lifePointReduce',
        sortable: true,
      }
    ];

    return (
      <Layout className="enemy-wrapper">
        <Layout.Content>
          <Table dataSource={enemys} columns={columns} />
        </Layout.Content>
        <Layout.Sider width={300} hideOn="mobile">
          <div style={{padding: '0 20px'}}>
            <p>
              <strong></strong><br/> 1=1
            </p>
            <p>
              <strong></strong><br/>1=1%
            </p>
            
            <ul className="desc">
              <li><strong>1. </strong>  =  -  5%5%()</li>
              <li><strong>2. </strong>  =  * (1 - %)</li>
            </ul>
          </div>
        </Layout.Sider>
      </Layout>
    );
  }
}
